.homepage {
  margin: 140px auto 70px auto;
}

.services {
  display: flex;
  align-items: center;
  justify-content: center;
}
.what-we-do {
  width: 50%;
}
.what-we-do__title {
  font-family: "Poppins", sans-serif;
  font-size: 44px;
  font-style: normal;
  font-weight: 700;
  line-height: 54px;
}
.what-we-do__body {
  margin: 30px 0;
  font-family: "Poppins", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 28px;
  color: #544837;
}
.what-we-do__btns {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}
.what-we-do__btns > a:nth-child(1) {
  height: 66px;
  width: 245px;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
  background-color: #0025b4;
  background-image: url(/procredita-home-page/static/media/hire-us-btn.bd500894.svg);
  background-repeat: no-repeat;
  background-size: contain;
}

.what-we-do__btns > a:nth-child(1):hover {
  box-shadow: 0 0 5px #0025b4;
  transition: all 200ms;
}

.what-we-do-btn__two span:nth-child(1) {
  height: 17px;
  width: 20px;
  background-image: url(/procredita-home-page/static/media/what-we-do-btn.ebfb4586.svg);
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
}
.what-we-do-btn__two span:nth-child(2) {
  font-family: Trueno;
  font-weight: 700;
  color: #0025b4;
}
.service__img-box {
  width: 50%;
  height: 400px;
  background-image: url(/procredita-home-page/static/media/service-image.10cc98e8.svg);
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  background-repeat: no-repeat;
  background-position: center;
}
/* -------BREAK POINTS @ 768px---------- */
@media (max-width: 768px) {
  .services {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .what-we-do {
    width: 100%;
    text-align: center;
  }
  .what-we-do__btns {
    flex-direction: column;
  }
  .service__img-box {
    width: 100%;
 
  }
}
